<template>
    <div :class="{ 'w-full': is_w_100, 'd-display': is_w_100 }">
        <div class="text-right float-left" :style="{ 'width': long }">
            <slot name="left"></slot>
        </div>
        <div class="text-left float-left">
            <slot name="right"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            long: {
                type: String,
                default: '170px'
            },
            is_w_100: {
                type: Boolean,
                default: true
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


