<template>
    <v-row>
        <v-col cols="6">
            <div class="fs-22 fw-b text-primary text-ls-L name">
                {{ $store.state.user.username }}
            </div>
            <div class="pl-5 text-primary-dark">
                ID: {{ $store.state.user.number_code }}
            </div>
        </v-col>
        <v-col cols="6" class="text-right pb-0">
            <div class="pt-5">
                {{ view.getTodayEn() }}
            </div>
            
            <div class="pl-3">
                Contents Period: 由&nbsp;{{ view.ser_timed($store.state.bonus.chronu.start, true) }}至&nbsp;{{ view.ser_timed($store.state.bonus.chronu.end, true) }}
            </div>
        </v-col>
    </v-row>
</template>

<script>
import UiClosedText from '../../../../components/Ui/UiClosedText.vue'
    export default {
  components: { UiClosedText },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
</style>


