<template>
    <page-center-layout>
        <div slot="title" class=" pa-3 pl-6">
            <div class="ma-0 d-inline-block">

                <ui-icon-button @signal_Father="back"></ui-icon-button>
            </div>
            <div class="ml-2 d-inline">

                <ui-icon-button @signal_Father="pdf" :icon="'mdi-file-pdf'"></ui-icon-button>
            </div>
        </div>

        <div slot="content" class="fs-14">
            <div class="page-header text-center pl-2 fs-24">{{ $store.state.user.display_name }}</div>

            <ui-content-title>
                <div class="fs-12 pl-1 pt-5 mb-3 first-title-light">Bonus Statement</div>
            </ui-content-title>

            <ui-content-panel :is_white="true">
                
            <!-- v-card class="mx-1 pa-4 mt-1 px-5" -->
                
                <!-- -->
                <user-center-msg :me="mine"></user-center-msg>

                <!-- -->
                <ui-content-title :is_sub_header="true">
                    <div class="fs-14 pt-5 pb-1">
                        Bonus Eligibility:  <span class="text-success">Active</span>
                    </div>
                </ui-content-title>

                <user-center-order></user-center-order>

                <!-- -->
                <ui-content-title :is_sub_header="true">
                    <div class="fs-14 pt-4 pb-1">
                        本周期支付級別:  <span class="text-pv">
                            <vip-label-simpler :item="$store.state.bonus.vip"></vip-label-simpler>
                        </span>
                    </div>
                </ui-content-title>

                <user-center-bonus v-if="$store.state.user"></user-center-bonus>

                <!-- -->
                <ui-content-title :is_sub_header="true" v-if="mine">
                    <div class="fs-14 pt-5 pb-1">
                        Notes:  <span class="text-success">
                        </span>
                    </div>
                </ui-content-title>

                <user-center-notes :me="mine"></user-center-notes>

                <div>&nbsp;</div>
            </ui-content-panel>
        </div>

        <div slot="extra" v-if="$store.state.users_sponser">

            <!-- -->

            <ui-content-title>
                <div class="fs-18 pl-1 pt-6 mb-4 first-title text-shadown-L">首購推薦獎金</div>
            </ui-content-title>

            <user-recommend v-if="childrens" :me="mine" :childs="childrens"></user-recommend>
            <loading v-else></loading>

            <!-- -->
            <ui-content-title>
                <div class="fs-18 pl-1 pt-10 mb-4 first-title text-shadown-L">活躍分店消費獎勵</div>
            </ui-content-title>

            <user-active-house v-if="childrens" :me="mine" :childs="childrens"></user-active-house>
            <loading v-else></loading>

            <!-- -->

            <ui-content-title>
                <div class="fs-18 pl-1 pt-10 mb-4 first-title text-shadown-L">團隊銷售獎金</div>
            </ui-content-title>

            <user-team-layer v-if="childrens" :me="mine" :childs="childrens"></user-team-layer>
            <loading v-else></loading>

            <div>&nbsp;</div>
            <div>&nbsp;</div>
        </div>

        <loading-huge v-else slot="extra"></loading-huge>
    </page-center-layout>
</template>

<script>
import api_bonus from '../../../common/es/api_bonus/index'

import TreeListLayout from '@/components/Layout/TreeListLayout.vue'
import UserBuys from './UserBuys.vue'
import UiPageHeader from '@/components/Ui/UiPageHeader.vue'
import UiContentTitle from '@/components/Ui/UiContentTitle.vue'
import UserCenterMsg from './Inner/UserCenterMsg.vue'
import UiIconButton from '../../../components/Ui/UiIconButton.vue'
import UserCenterOrder from './Inner/UserCenterOrder.vue'
import VipLabel from '../../../components/Tree/VipLabel.vue'
import VipLabelSimpler from '../../../components/Tree/VipLabelSimpler.vue'
import UserCenterBonus from './Inner/UserCenterBonus.vue'
import PageCenterLayout from '../../../components/Layout/PageCenterLayout.vue'
import UiContentPanel from '../../../components/Ui/UiContentPanel.vue'
import UserActiveHouse from './Bonus/UserActiveHouse.vue'
import Loading from '../../../components/Data/Loading.vue'
import LoadingHuge from '../../../components/Data/LoadingHuge.vue'
import UserRecommend from './Bonus/UserRecommend.vue'
import UserTeamLayer from './Bonus/UserTeamLayer.vue'
import UserCenterNotes from './Inner/UserCenterNotes.vue'

    export default {
        components: { TreeListLayout, UserBuys, UiPageHeader, UiContentTitle, UserCenterMsg, UiIconButton, UserCenterOrder,
                VipLabel,
                VipLabelSimpler,
                UserCenterBonus,
                PageCenterLayout,
                UiContentPanel,
                UserActiveHouse,
                Loading,
                LoadingHuge,
                UserRecommend,
                UserTeamLayer,
                UserCenterNotes },
        name: '',
        data() {
            return {
                mine: null,
                childrens: null
            }
        },
        mounted() {
            this.lockMine()
        },
        methods: {
            pdf() {

            },
            lockMe(user, transfer) {
                if (user.unid == this.$store.state.user.unid) {

                    let res = JSON.parse(JSON.stringify(user))
                    this.mine = res
                }
            },
            lockChilds(user, transfer) {
                if (user.unid != this.mine.unid) {
                    this.childrens.push(user)
                }
            },
            async lockMine() {
                let many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                
                if (many) {
                    api_bonus._build_res(many, transfer, this.lockMe)
                }

                const usd_tl = await api_bonus.team_layer( [ this.mine ], transfer)
                const usd_re = await api_bonus.recommend( [ this.mine ], transfer )

                console.log(this.mine)
                if (this.mine) {
                    this.childrens = [ ]
                    api_bonus._build_res([ this.mine ], transfer, this.lockChilds)
                }
            },
            back() {
                this.$router.push({
                    path: '/home/account' })
            }
        }
    }
</script>

<style lang="sass" scoped>
.first-title-light
    color: #9c9c9c

.first-title
    color: #7a7b7b

.page-header
    color: #5a5a5b
    padding-left: 0px
    line-height: 2.6vw
.page-sub
    color: #979797
</style>


