        </slot>
    </div>
</template>

<script>
    export default {
        props: {
            br_size: {
                type: String,
                default: '12px'
            },
            is_white: {
                type: Boolean,
                default: false
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.ui-panel
    min-height: 2vh
    background: rgba(255, 255, 255, 0.8)
</style>


