<template>
    <div :class="{ 'd-inline': !block }" v-html="show_vip"></div>
</template>

<script>
import api_bonus from '@/common/es/api_bonus/index'

    export default {
        props: {
            item: {
                type: String
            },
            block: {
                type: Boolean,
                default: false
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        computed: {
            show_vip() {
                if (this.item) { 
                    let res = api_bonus.vip_view.split_code(this.item)
                    res = api_bonus.vip_view.code_to_content( res )
                    if (res) { return res.name } 
                }
                return '无'
            },
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


