<template>
    <div>
        <ui-content-panel>
            <ui-bonus-table>
                <thead slot="header">
                    <tr class="text-center">
                        <td>會員號碼</td>
                        <td>會員名稱</td>
                        <td>支付代數</td>
                        <td>訂單編號</td>
                        <td>訂單點數</td>
                        <td>獎金百分比</td>
                        <td width="12%">獎金點數</td>
                    </tr>
                </thead>
                <tbody slot="body">
                    <tr v-for="(v, i) in buying" :key="i" class="text-center">
                        <td>
                            {{ v.users_permissions_user.number_code }}
                        </td>
                        <td class="text-left pl-2">
                            {{ v.users_permissions_user.display_name }}
                        </td>
                        <td>
                            {{ v.user_sponser.temporary_index }}
                        </td>
                        <td>{{ v.order_code }}</td>
                        <td>
                            {{ v.product.pv * v.num }}
                        </td>
                        <td>
                            {{ view.floatMul(me.vip.ratio[v.user_sponser.temporary_index - 1],  100) }}%
                        </td>
                        <td>
                            {{ view.floatMul(me.vip.ratio[v.user_sponser.temporary_index - 1],  v.user_sponser.ipv) }}
                        </td>
                    </tr>
                </tbody>
            </ui-bonus-table>

            <v-row class="mt-2">
                <v-col cols="8">

                </v-col>
                <v-col cols="2" class="text-right">
                    團隊銷售獎金
                </v-col>
                <v-col cols="2" class="pl-2">
                    <p class="fw-b" v-if="me">
                        HK${{ me.bonus.usd_team_layer }}
                    </p>
                </v-col>
            </v-row>
        </ui-content-panel>
    </div>
</template>

<script>
import UiBonusTable from '../../../../components/Ui/UiBonusTable.vue'
import UiContentPanel from '../../../../components/Ui/UiContentPanel.vue'
    export default {
  components: { UiContentPanel, UiBonusTable },
        name: '',
        props: {
            childs: {
                type: Array
            },
            me: {
                type: Object
            }
        },
        data() {
            return {
                buying: [ ]
            }
        },
        mounted() {
            this.buyingLoading()
        },
        methods: {

            buyingLoading() {

                this.buying = [ ]
                this.childs.map(async e => {

                    let res = await this.conn.get(
                        this.api.buys,
                        this.$store.state.token,
                        {
                            'chronu': this.$store.state.chronus.id,
                            'users_permissions_user': e.id,
                            '_sort': 'published_at:DESC'
                        }
                    )

                    if (res) {
                        res.map(j => {
                            j.user_sponser = e
                            this.buying.push(j)
                        })
                    }
                })
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


