<template>
    
    <div class="title pl-4 mt-2 pt-2" 
        :class="{ 'text-center': is_center, 'd-inline': is_block }" 
        :style="{ 'font-size': size }"
        >

        <slot>

        </slot>

    </div>

</template>

<script>
    export default {
        props: {
            is_block: {
                type: Boolean,
                default: false
            },

            is_center: {

                type: Boolean,
                default: true
            },

            size: {
                type: String,
                default: '24px'
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


