<template>
    <div class="pl-5 pb-2">

        <ui-black-line-table>
            <thead slot="header">
                <tr>
                    <td>Individual Credit</td>
                    <td>Organization Credit</td>
                    <td>直推 Active 下線</td>
                </tr>
            </thead>

            <tbody slot="body" v-if="me">
                <tr>
                    <td class="py-1">
                        {{ me.ipv }}
                    </td>
                    <td >
                        {{ me.son_pv }}
                    </td>
                    <td>
                        {{ me.son_can_num }}
                    </td>
                </tr>
            </tbody>
        </ui-black-line-table>

        <loading v-if="me == null"></loading>
    </div>
</template>

<script>
import api_bonus from '../../../../common/es/api_bonus/index'
import Loading from '../../../../components/Data/Loading.vue'
import UiBlackLineTable from '../../../../components/Ui/UiBlackLineTable.vue'
    export default {
  components: { UiBlackLineTable, Loading },
        name: '',
        data() {
            return {
                user_s: null,
                me: null
            }
        },
        mounted() {
            this.getMine()
        },
        methods: {
            lockMe(user, transfer) {
                if (user.unid == this.$store.state.user.unid) {
                    this.me = user
                }
            },
            getMine() {
                let many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                
                if (many) {

                    api_bonus._build_res(many, transfer, this.lockMe)
                }

            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


