<template>
    <div class="pl-5">

        <ui-black-line-table>
            <thead slot="header">
                <tr>
                    <td>Order Date</td>
                    <td>Member ID</td>
                    <td>Role</td>
                    <td>Order No.</td>
                    <td>Order Points</td>
                </tr>
            </thead>

            <tbody slot="body">
                <tr v-for="(v, i) in buys" :key="i">
                    <td class="py-1">
                        {{ view.ser_timed(v.buy_time, true) }}
                    </td>
                    <td>
                        {{ $store.state.user.number_code }}
                    </td>
                    <td>
                        Member
                    </td>
                    <td>
                        {{ v.order_code }}
                    </td>
                    <td>
                        {{ v.num * v.product.pv }}
                    </td>
                </tr>
            </tbody>
        </ui-black-line-table>

        <div colspan="5" class="pt-2 pr-0 fs-15 text-right" style="width: 64%">
            Total Credit:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            {{ result }}
        </div>
    </div>
</template>

<script>
import UiBlackLineTable from '../../../../components/Ui/UiBlackLineTable.vue'
    export default {
  components: { UiBlackLineTable },
        name: '',
        data() {
            return {
                buys: [ ],
                result: 0
            }
        },
        mounted() {
            this.buysLoading()
        },
        methods: {
            async buysLoading() {
                const buys = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token,
                    {
                        'users_permissions_user': this.$store.state.user.id
                    }
                )

                if (buys) {
                    this.buys = buys

                    buys.map(e => {
                        this.result += (e.num * e.product.pv)
                    })
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


