<template>
    
    <div class="" 
        :class="{ 'text-center': is_center, 'ui-sub-header': is_sub_header }" 
        :style="{ 'font-size': size }"
        >

        <slot>

        </slot>

    </div>

</template>

<script>
    export default {
        props: {
            is_sub_header: {
                type: Boolean,
                default: false
            },

            is_center: {

                type: Boolean,
                default: false
            },

            size: {
                type: String,
                default: '12px'
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.ui-sub-header
    color: #9e9e9e
</style>


