<template>
    
    <v-btn
        class="ma-0 text-center d-inline"
        fab
        icon
        :outlined="is_outlined"
        :color="color"
        x-small

        @click="signal_Father"
    >
        <v-icon v-html="icon">
            
        </v-icon>
    </v-btn>

</template>

<script>
    export default {
        props: {
            icon: {
                type: String,
                default: 'mdi-chevron-left'
            },
            is_outlined: {
                type: Boolean,
                default: true
            },
            color: {
                type: String,
                default: ''
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            signal_Father() {
                this.$emit('signal_Father')
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


