<template>
    <table class="table-black-line" :style="{ 'width': w }">
        <slot name="header">

        </slot>

        <slot name="body">

        </slot>
    </table>
</template>

<script>
    export default {
        props: {
            w: {
                type: String,
                default: '64%'
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.table-black-line
    border-bottom: 1px solid rgba(120, 120, 120, 0.8) !important
    border-collapse: 1 !important
    border-radius: 0px

.table-black-line td
    padding: 3px 6px
    padding-left: 6px
</style>


